.checkout_product {
  display: flex;
  margin: 20px 0;
  padding: 20px;
  background-color: white;
  border-bottom: 1px solid #eee;
}

.checkoutProduct_image {
  object-fit: contain;
  width: 100px;
  height: 50px;
}

.product_info {
  padding-left: 20px;
}

.product_title {
  font-size: 17px;
  font-weight: 600;
}

.product_price {
  margin-top: 5px;
}

.product_rating {
  display: flex;
}

button {
  background: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  padding: 8px;
  border-radius: 3px;
  margin-top: 10px;
  cursor: pointer;
}

button:hover {
  background: #e4b847;
}
